.Button{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    color: white;
    margin: 10px;
    padding: 10px;
    background-color: #333333;
    cursor: pointer;
    user-select: none;
    border-radius: 5px;
    &:hover{
        background-color: #005800;
    }
    &:active{
        background-color: #003900;
    }
    &.Active:hover{
        background-color: #4d794d;
    }
    &.Active{
        background-color: rgb(22, 108, 22);
    }
}
.ruler{
    background-image: url("../assets/images/ruler.png");
    background-repeat: repeat-x;
    animation: 1s ruler infinite linear forwards;
    background-color: #ffffff20!important;
    cursor: unset;
}
@keyframes ruler {
    0%{
        background-position-x: 0%;
    }
    100%{
        background-position-x: 100%;
    }
}