.Header{
    grid-area: h;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    z-index: 10;
}
.leftSide,.rightSide{
    display: flex;

}
.Back{
    padding: 10px 20px;
}
.AuthButton{
    align-self: center;
}
.logo{
    padding: 0.3%;
    margin: 5px;
    height: 80%;
    width: 60px;
    filter:invert(1);
    justify-content: start;
    align-items: center;
    margin-left: 20px;
}